<template>
    <div id="app">
        <nav id="navbar-example" class="navbar navbar-light bg-light">
            <span id="curinga">
                <div class="card" v-for="item in curingas" :key="item" :class="extrairNype(item)">
                    <p>{{ extrairCarta(item) }}</p>
                </div>
            </span>
            <span id="jogadores">
                <div class="jogador" v-for="item in jogadores" :key="item.nome">
                    <span>
                        <img :src="'https://api.adorable.io/avatars/60/'+item.nome+'.png'" alt="">
                    </span>
                    <p>{{ item.nome }} <span class="badge" :class="'cor-'+item.quantidadeCartas">{{ item.quantidadeCartas }}</span></p>
                </div>
            </span>

            <ul class="nav nav-pills">
                <li class="nav-item">
                    <input @click="embaralhar()" class="btn btn-sm btn-success" type="button" value="Dar Cartas">
                </li>
                <li class="nav-item">
                    <input @click="novaRodada()" class="btn btn-sm btn-warning" type="button" value="LimparMesa">
                </li>
                <li class="nav-item">
                    <button v-if="!estaLogado"  @click="entrar()" type="button" class="btn btn-sm btn-primary">Entrar</button>
                    <input v-if="estaLogado" @click="sair()" class="btn btn-sm btn-danger" type="button" value="Sair">
                </li>
            </ul>
        </nav>

        <div id="mesa">
            <div class="hand">
                <div class="card" v-for="item in mesa" :key="item" :class="extrairNype(item.carta)">
                    <p>{{ extrairCarta(item.carta) }}</p>
                    <span>{{item.jogador}}</span>
                </div>
            </div>
        </div>

        <div id="minhas">
            <div class="hand grow">
                <div class="card" v-for="(item, index) in cartas" :key="item" @click="jogar(index)" :class="extrairNype(item)">
                    <p>{{ extrairCarta(item) }}</p>
                </div>
            </div>
        </div>

        <div class="modal" tabindex="-1" role="dialog">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Modal title</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <p>Modal body text goes here.</p>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        <button type="button" class="btn btn-primary">Save changes</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: 'App',
        data: function () {
            return {
                nome: null,
                senha: null,
                quantidade: 0,
                mesa: [],
                curingas: [],
                jogadores: [],
                cartas: []
            }
        },
        components: {},
        mounted: function () {
            if (window.localStorage) {
                if (window.localStorage.getItem('nome') && window.localStorage.getItem('senha')) {
                    this.nome = window.localStorage.getItem('nome')
                    this.senha = window.localStorage.getItem('senha')
                }
            }
            setInterval(() => {
                this.sala();
                this.getCartas();
            }, 1000);
        },
        computed: {
            estaLogado() {
                return (this.nome != null && this.senha != null)
            }
        },
        methods: {
            extrairNype(carta) {
                if (carta) {
                    let nype = carta.substr(0, 1) == '1' ? carta.substr(2, 1) : carta.substr(1, 1);
                    switch (nype) {
                        case "♦":
                            return "suitdiamonds";
                        case "♥":
                            return "suithearts";
                        case "♣":
                            return "suitclubs";
                        case "♠":
                            return "suitspades";
                        default:
                            return "0"
                    }
                }
            },
            extrairBaralho(carta) {
                return carta.substr(0, 1) == '1' ? carta.substr(3, 1) : carta.substr(2, 1);
            },
            extrairCarta(carta) {
                return carta.substr(0, 1) == '1' ? carta.substr(0, 2) : carta.substr(0, 1);
            },
            entrar() {
                let nome = prompt('Seu nome ?')
                this.nome = nome
                axios.post("http://localhost:3000/salas/entrar",
                    {
                        "nome": this.nome
                    }
                ).then((response) => {
                    this.senha = response.data.senha
                    window.localStorage.setItem('nome', this.nome)
                    window.localStorage.setItem('senha', this.senha)
                });
            },
            sair() {
                axios.post("http://localhost:3000/salas/sair",
                    {
                        "nome": this.nome,
                        "senha": this.senha
                    }
                ).then(() => {
                    this.nome = null
                    this.senha = null
                    this.cartas = []
                    window.localStorage.removeItem('nome')
                    window.localStorage.removeItem('senha')
                });
            },
            embaralhar() {
                let qtd = prompt('Quantas cartas ?')
                axios.post("http://localhost:3000/salas/embaralhar",
                    {
                        "quantidade": qtd
                    }
                );
            },
            jogar(index) {
                axios.post("http://localhost:3000/salas/jogar",
                    {
                        "posicaoCarta": index,
                        "nome": this.nome,
                        "senha": this.senha,
                    }
                ).then((response) => {
                    this.sala()
                    this.cartas = response.data.cartas
                });
            },
            sala() {
                axios.get("http://localhost:3000/salas")
                    .then((response) => {
                        this.mesa = response.data.mesa;
                        this.jogadores = response.data.jogadores;
                        this.curingas = response.data.curingas
                    })
            },
            novaRodada() {
                axios.post("http://localhost:3000/salas/novaRodada")
            },
            getCartas() {
                if (this.nome != null && this.senha != null) {
                    axios.post("http://localhost:3000/salas/cartas",
                        {
                            "nome": this.nome,
                            "senha": this.senha
                        })
                        .then((response) => {
                            this.cartas = response.data.cartas
                        })
                }
            }
        }
    }
</script>

